.tutorial {
  padding-top: 70px;
  background-color: #bbfffd;
  padding-bottom: 30px;
  .tutorial__title {
    text-align: center;
    margin-top: 20px;
  }
  .group__offer {
    text-align: center;
    margin-top: 20px;
    color: #595959;
  }
  .group__wrapper {
    display: flex;
    width: 65%;
    margin: 20px auto;
    justify-content: space-between;
    .btn__group {
      color: #ffffff;
      border-radius: 5px;
      &:hover, &:focus {
        box-shadow: 0 0.5em 0.5em -0.4em #092ad3;
        transform: translatey(-0.5em);
      }
    }
    .group1 {
      background-color: red;
    }
    .group2 {
      background-color: yellow;
    }
    .group3 {
      background-color: green;
    }
    .group4 {
      background-color: cadetblue;
    }
    .group5 {
      background-color: blue;
    }
    .group6 {
      background-color: brown;
    }
  }
  .page__wrapper{
    display: flex;
    width: 120px;
    margin: 0 auto;
    justify-content: space-between;
    align-items: center;
    .next-btn, .btn-prev {
      background-color: transparent;
      border: none;
      color: blue;
      font-size: 25px;;
      font-weight: 700;
    }
    .counter__page {
      font-size: 30px;
    }
  }
  .game__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60%;
    margin: 0 auto;
    gap: 20px;
    .game__btn {
      background-color: transparent;
      border-radius: 10px;
      transition: 0.2s;
      &:hover, &:focus {
        box-shadow: 0 0.5em 0.5em -0.4em #092ad3;
        color: red;
        transform: translatey(-0.5em);
      }
    }
  }
}

