.modal-auth {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  z-index: 999;
  .modal-dialog {
    max-width: 780px;
    width: 95%;
    background: #ffffff;
    border-radius: 5px;
    margin: auto;
    padding: 40px 45px;
  }
  .modal-dialog-auth {
    width: auto;
    position: relative;
  }
  .close-auth {
    font-size: 36px;
    border: none;
    background-color: transparent;
    position: absolute;
    top: 10px;
    right: 20px;
    cursor: pointer;
  }
  fieldset {
    padding: 0.35em 0.75em 0.625em;
  }
  .modal-body {
    margin-bottom: 22px;
    border: 2px solid rgb(192, 192, 192);
    .modal-title {
      margin: 0;
      font-weight: bold;
      font-size: 36px;
      line-height: 42px;
    }
    .label-auth {
      display: block;
      margin: 30px;

      span {
        width: 80px;
        display: inline-block;
        &::after {
          content: '*';
          color: red;
        }
      }

      input {
        &:invalid {
          border-color: red;
        }
        &:valid {
          border-color: green;
        }
      }

    }
  }
}

.modal-footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  .footer-buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    .link {
      color: #1890ff;
      cursor: pointer;
    }
    .button-primary {
      background: #1890ff;
      border: 1px solid #1890ff;
      color: #fff;
      margin-right: 10px;
    }
  }
}













